import React, { FC, ReactNode, useEffect, useState } from 'react';
import {
  generateViewport,
  Viewport,
  ViewportContext,
} from './utilities/ViewportContext';
import {
  HashRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';
import { NavBar } from './components/generic';
import { PageContainer } from './components/layout';
import { Route as RouteType, routes } from './routes';
import { Box } from '@chakra-ui/core';

const App: FC = () => {
  const [viewport, setViewport] = useState<Viewport>(generateViewport(window));

  useEffect(() => {
    const handleViewportChange = () => setViewport(generateViewport(window));
    window.addEventListener('resize', handleViewportChange);
    return () => window.removeEventListener('resize', handleViewportChange);
  }, []);

  // generate all page routes recursively
  const generatedRoutes: ReactNode[] = [];

  const generateRoutes = (routes: RouteType[], parentPath = ''): void => {
    routes.forEach(({ path, component, childrenRoutes }) => {
      generatedRoutes.push(
        <Route
          key={`${parentPath}${path}`}
          exact
          path={`${parentPath}${path}`}
          component={() => <PageContainer>{component}</PageContainer>}
        />
      );
      if (childrenRoutes?.length) generateRoutes(childrenRoutes, path);
    });
  };

  generateRoutes(routes);

  return (
    <ViewportContext.Provider value={viewport}>
      <Router>
        <Box
          position={'fixed'}
          w={'100%'}
          zIndex={999}
          top={0}
          left={0}
          bg={'white'}
        >
          <NavBar />
        </Box>
        <Box mt={[10, 12, 16]}>
          <Switch>
            {generatedRoutes}
            <Route path={'/'}>
              <Redirect to={'/home'} />
            </Route>
            <Route path="*" exact component={() => <Redirect to={'/'} />} />
          </Switch>
        </Box>
      </Router>
    </ViewportContext.Provider>
  );
};

export default App;
