import React, { FC, useState } from 'react';
import { LabLead } from '../../../types/labLead';
import {
  Box,
  Collapse,
  Flex,
  Link,
  PseudoBox,
  SimpleGrid,
  Text,
} from '@chakra-ui/core';
import {
  Clipboard,
  Coffee,
  FileText,
  Globe,
  Mail,
  PenTool,
  Users,
} from 'react-feather';
import { Image, InnerImage } from '../../generic';
import { colorfulGreenBackgroundImage } from '../../../static';
import { avatarPlaceholder } from '../../../utilities';

interface FancyCardProps {
  labLead: LabLead;
}

export const FancyCard: FC<FancyCardProps> = ({
  labLead: {
    avatar,
    firstName,
    lastName,
    salutation,
    position,
    email,
    introduction,
    researchInterests,
    academicMemberships,
    professionalMemberships,
    teachingList,
    cv,
  },
}) => {
  const iconSize = '24px';

  const fields = [
    {
      icon: <Mail size={iconSize} />,
      component: (
        <Link
          href={`mailto:${email}`}
          target={'_blank'}
          rel={'noopener noreferrer'}
          color={'mustard-yellow.500'}
          fontWeight={'600'}
        >
          {email}
        </Link>
      ),
    },
    {
      icon: <Coffee size={iconSize} />,
      component: introduction.map((paragraph, index) => (
        <Text key={index}>{paragraph}</Text>
      )),
    },
  ].concat(
    cv
      ? [
          {
            icon: <FileText size={iconSize} />,
            component: (
              <PseudoBox
                backgroundColor={'mustard-yellow.500'}
                color={'white'}
                _hover={{
                  cursor: 'pointer',
                  backgroundColor: 'mustard-yellow.400',
                }}
                borderRadius={8}
                py={2}
              >
                <Link
                  href={cv}
                  target={'_blank'}
                  rel={'noopener noreferrer'}
                  px={8}
                  _hover={{ textDecoration: 'none' }}
                  _focus={{ border: 'none' }}
                >
                  Get CV
                </Link>
              </PseudoBox>
            ),
          },
        ]
      : []
  );

  const [showMore, setShowMore] = useState<boolean>(false);

  const collapsibleFields = [
    {
      icon: <Clipboard size={iconSize} />,
      title: 'Research Interests',
      list: researchInterests,
    },
    {
      icon: <PenTool size={iconSize} />,
      title: 'Academic Memberships',
      list: academicMemberships,
    },
    {
      icon: <Globe size={iconSize} />,
      title: 'Professional Memberships',
      list: professionalMemberships,
    },
    {
      icon: <Users size={iconSize} />,
      title: 'Teaching List',
      list: teachingList,
    },
  ];

  return (
    <Box
      p={4}
      color={'white'}
      borderRadius={'1rem'}
      position={'relative'}
      overflow={'hidden'}
    >
      <InnerImage
        src={colorfulGreenBackgroundImage}
        alt={'colorful green background'}
      />

      <Box position={'relative'}>
        <SimpleGrid columns={[1, 3, 3, 4]} spacing={8}>
          <Box gridColumn={'span 1'} my={2}>
            <Box
              w={'100%'}
              mx={'auto'}
              maxW={'200px'}
              borderWidth={4}
              borderRadius={'50%'}
              borderColor={'mustard-yellow.500'}
            >
              <Image
                src={avatar || avatarPlaceholder}
                alt={firstName}
                circular
              />
            </Box>
          </Box>

          <Box gridColumn={['span 1', 'span 2', 'span 2', 'span 3']}>
            <Flex
              alignItems={['left', 'baseline']}
              direction={['column', 'row']}
            >
              <Text
                fontSize={'2xl'}
                fontWeight={'bold'}
              >{`${salutation} ${firstName} ${lastName}`}</Text>

              <Text fontWeight={'semibold'} mx={[0, 0, 2]}>
                {position}
              </Text>
            </Flex>

            <Box my={4}>
              {fields.map(({ icon, component }, index) => (
                <Flex key={index} alignItems={'center'} my={2}>
                  <Box color={'mustard-yellow.500'} mr={[4, 6]}>
                    {icon}
                  </Box>
                  <Box>{component}</Box>
                </Flex>
              ))}
            </Box>
          </Box>
        </SimpleGrid>

        <Box my={4} mx={4}>
          <Collapse isOpen={showMore} duration={500} animateOpacity={true}>
            <SimpleGrid columns={[1, 1, 2]} spacing={4}>
              {collapsibleFields.map(({ icon, title, list }, index) => (
                <Flex
                  key={index}
                  gridColumn={'span 1'}
                  alignItems={'flex-start'}
                >
                  <Box color={'mustard-yellow.500'} mr={[4, 6]}>
                    {icon}
                  </Box>
                  <Box>
                    <Text fontSize={'lg'} fontWeight={'bold'}>
                      {title}
                    </Text>
                    <Box>
                      {list.map((element, index) => (
                        <Flex key={index}>
                          <Box p={2}>
                            <Box
                              bg={'white'}
                              w={'0.25rem'}
                              h={'0.25rem'}
                              borderRadius={'50%'}
                            />
                          </Box>
                          <Text>{element}</Text>
                        </Flex>
                      ))}
                    </Box>
                  </Box>
                </Flex>
              ))}
            </SimpleGrid>
          </Collapse>

          <Text
            color={'mustard-yellow.400'}
            fontWeight={'bold'}
            w={'100%'}
            textAlign={'center'}
            cursor={'pointer'}
            onClick={() => setShowMore(!showMore)}
          >{`See ${showMore ? 'Less' : 'More'}`}</Text>
        </Box>
      </Box>
    </Box>
  );
};
