import React, { CSSProperties, FC } from 'react';

interface InnerImageProps {
  src: string;
  alt: string;
  style?: CSSProperties;
}

export const InnerImage: FC<InnerImageProps> = ({ src, alt, style }) => (
  <img
    src={src}
    alt={alt}
    style={{
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      height: '100%',
      width: '100%',
      objectFit: 'cover',
      objectPosition: 'center',
      ...style,
    }}
  />
);
