import React, { FC, ReactNode, useState } from 'react';
import {
  Box,
  Menu,
  MenuItem,
  MenuList,
  PseudoBox,
  Text,
} from '@chakra-ui/core';
import { Link } from 'react-router-dom';
import { useViewport } from '../../../hooks';

export interface NavItemProps {
  title: string;
  basePath: string;
  items?: { path: string; child: ReactNode }[];
}

export const NavItem: FC<NavItemProps> = ({ title, basePath, items }) => {
  const { isMobile } = useViewport();
  const [isOpen, setIsOpen] = useState<boolean>(false);

  return (
    <>
      {!isMobile ? (
        // desktop version nav item
        <Box
          position={'relative'}
          onMouseEnter={() => setIsOpen(true)}
          onMouseLeave={() => setIsOpen(false)}
          color={'jet-black.500'}
        >
          <Link
            to={basePath}
            style={{ textDecoration: 'none', color: 'inherit' }}
          >
            <PseudoBox
              _hover={{ borderColor: 'emerald-green.500' }}
              borderBottom={'2px'}
              borderColor={'transparent'}
              py={1}
              fontSize={['md', 'lg']}
              fontWeight={'bold'}
            >
              <Text m={0} textAlign={'center'}>
                {title}
              </Text>
            </PseudoBox>
          </Link>
          {items && (
            <Menu isOpen={isOpen} autoSelect={false}>
              <MenuList p={0} minW={'10rem'} w={'100%'}>
                {items.map(({ path, child }, index) => (
                  <MenuItem
                    key={index}
                    border={'none'}
                    backgroundColor={'transparent'}
                  >
                    <Link
                      to={`${basePath}${path}`}
                      style={{
                        textDecoration: 'none',
                        color: 'inherit',
                        width: '100%',
                        padding: '1rem 0',
                      }}
                      onClick={() => setIsOpen(false)}
                    >
                      {child}
                    </Link>
                  </MenuItem>
                ))}
              </MenuList>
            </Menu>
          )}
        </Box>
      ) : (
        // mobile version nav item
        <Box
          my={1}
          borderBottom={'1px'}
          borderColor={'emerald-green.400'}
          fontSize={'md'}
          color={'jet-black.500'}
        >
          <Link
            to={basePath}
            style={{
              textDecoration: 'none',
              color: 'inherit',
              padding: '0.8rem 0',
            }}
          >
            <Text my={1} fontWeight={'600'}>
              {title}
            </Text>
          </Link>
          <Box ml={3}>
            {items?.map(({ path, child }, index) => (
              <Box key={index} my={1} fontWeight={'400'}>
                <Link
                  key={index}
                  to={`${basePath}${path}`}
                  style={{
                    textDecoration: 'none',
                    color: 'inherit',
                    width: '100%',
                    padding: '0.8rem 0',
                  }}
                >
                  {child}
                </Link>
              </Box>
            ))}
          </Box>
        </Box>
      )}
    </>
  );
};
