import React, { CSSProperties, FC } from 'react';
import { Box } from '@chakra-ui/core';
import { InnerImage } from './InnerImage';

interface ImageProps {
  src: string;
  alt: string;
  circular?: boolean;
  style?: CSSProperties;
  className?: string;
  innerImageStyle?: CSSProperties;
}

export const Image: FC<ImageProps> = ({
  src,
  alt,
  circular,
  style,
  className,
  innerImageStyle,
}) => (
  <Box
    position={'relative'}
    h={'100%'}
    w={'100%'}
    className={className}
    style={{
      paddingTop: circular ? '100%' : 0,
      borderRadius: circular ? '50%' : 0,
      overflow: 'hidden',
      ...style,
    }}
  >
    <InnerImage src={src} alt={alt} style={innerImageStyle} />
  </Box>
);
