import React, { FC } from 'react';
import moment from 'moment';
import { Box, Flex, Link, PseudoBox, SimpleGrid, Text } from '@chakra-ui/core';
import { Calendar, Coffee, FileText, Mail } from 'react-feather';
import { Member } from '../../../types/member';
import { Image } from '../../generic';
import { avatarPlaceholder } from '../../../utilities';

interface CardProps {
  member: Member;
}

export const Card: FC<CardProps> = ({
  member: {
    avatar,
    firstName,
    lastName,
    salutation,
    position,
    email,
    introduction,
    affiliationPeriodStart,
    affiliationPeriodEnd,
    cv,
  },
}) => {
  const iconSize = '24px';

  const fields = [
    {
      icon: <Mail size={iconSize} />,
      component: (
        <Link
          href={`mailto:${email}`}
          target={'_blank'}
          rel={'noopener noreferrer'}
          color={'tory-blue.500'}
          fontWeight={'600'}
        >
          {email}
        </Link>
      ),
    },
    {
      icon: <Coffee size={iconSize} />,
      component: introduction.map((paragraph, index) => (
        <Text key={index}>{paragraph}</Text>
      )),
    },
  ]
    .concat(
      affiliationPeriodStart
        ? [
            {
              icon: <Calendar size={iconSize} />,
              component: (
                <Box>
                  <Text
                    display={'inline-block'}
                    mr={1}
                  >{`Affiliated since `}</Text>
                  <Text display={'inline-block'} mr={1} fontWeight={'bold'}>
                    {moment(affiliationPeriodStart).format('MMMM YYYY')}
                  </Text>
                  {affiliationPeriodEnd && (
                    <>
                      <Text display={'inline-block'} mr={1}>{` till `}</Text>
                      <Text display={'inline-block'} mr={1} fontWeight={'bold'}>
                        {moment(affiliationPeriodEnd).format('MMMM YYYY')}
                      </Text>
                    </>
                  )}
                </Box>
              ),
            },
          ]
        : []
    )
    .concat(
      cv
        ? [
            {
              icon: <FileText size={iconSize} />,
              component: (
                <PseudoBox
                  backgroundColor={'emerald-green.500'}
                  color={'white'}
                  _hover={{
                    cursor: 'pointer',
                    backgroundColor: 'emerald-green.400',
                  }}
                  borderRadius={8}
                  py={2}
                >
                  <Link
                    href={cv}
                    target={'_blank'}
                    rel={'noopener noreferrer'}
                    px={8}
                    _hover={{ textDecoration: 'none' }}
                    _focus={{ border: 'none' }}
                  >
                    Get CV
                  </Link>
                </PseudoBox>
              ),
            },
          ]
        : []
    );

  return (
    <SimpleGrid columns={[1, 3, 3, 4]} spacing={8}>
      <Box gridColumn={'span 1'} my={2}>
        <Box
          w={'100%'}
          mx={'auto'}
          maxW={'200px'}
          borderWidth={4}
          borderRadius={'50%'}
          borderColor={'emerald-green.500'}
        >
          <Image src={avatar || avatarPlaceholder} alt={firstName} circular />
        </Box>
      </Box>

      <Box gridColumn={['span 1', 'span 2', 'span 2', 'span 3']}>
        <Flex alignItems={['left', 'baseline']} direction={['column', 'row']}>
          <Text fontSize={'xl'} fontWeight={'bold'}>{`${
            salutation ? `${salutation} ` : ``
          }${firstName} ${lastName}`}</Text>

          <Text fontWeight={'light'} mx={[0, 0, 2]}>
            {position}
          </Text>
        </Flex>

        <Box my={4}>
          {fields.map(({ icon, component }, index) => (
            <Flex key={index} alignItems={'center'} my={2}>
              <Box color={'emerald-green.500'} mr={[4, 6]}>
                {icon}
              </Box>
              <Box>{component}</Box>
            </Flex>
          ))}
        </Box>
      </Box>
    </SimpleGrid>
  );
};
