import React from 'react';
import { Text } from '@chakra-ui/core';
import { NavItem, NavItemProps } from './NavItem';
import { routes } from '../../../routes';

export const navItemsProps: NavItemProps[] = routes.map(
  ({ path, title, childrenRoutes }) => ({
    basePath: path,
    title,
    items: childrenRoutes?.map(({ path, title }) => ({
      path,
      child: <Text>{title}</Text>,
    })),
  })
);

export const navItems = navItemsProps.map(({ title, basePath, items }) => (
  <NavItem title={title} basePath={basePath} items={items} />
));
