import React, { FC, useState } from 'react';
import { Box, Collapse, Flex, Text } from '@chakra-ui/core';
import { AlignJustify, X } from 'react-feather';
import { navItems } from './navItems';

export const MobileNavBar: FC = () => {
  const [show, setShow] = useState<boolean>(false);

  return (
    <Box position={'relative'}>
      <Flex
        w={'100%'}
        backgroundColor={'emerald-green.500'}
        py={2}
        px={3}
        alignItems={'center'}
        color={'white'}
      >
        <Box w={'fit-content'} onClick={() => setShow(!show)} mx={2}>
          {!show ? <AlignJustify size={'30px'} /> : <X size={'30px'} />}
        </Box>
        <Text mx={2} fontWeight={'bold'}>
          Healthy Cities Lab at HKU
        </Text>
      </Flex>
      <Collapse isOpen={show} position={'fixed'} w={'100%'}>
        <Flex
          w={'100%'}
          backgroundColor={'white'}
          px={3}
          py={2}
          direction={'column'}
          onClick={() => setShow(false)}
        >
          {navItems.map((navItem, index) => (
            <Box key={index} w={'100%'}>
              {navItem}
            </Box>
          ))}
        </Flex>
      </Collapse>
    </Box>
  );
};
